import React, { useState } from "react";

function SlideShow({ data }) {
  const [slideIndex, setSlideIndex] = useState(0);

  function plusSlides(n) {
    if (data.length === slideIndex + n) setSlideIndex(0);
    else if (-1 === slideIndex + n) setSlideIndex(data.length - 1);
    else setSlideIndex(slideIndex + n);
  }

  return (
    <div>
      <div className="container-inside-modal">
        <div className="dummy"></div>
        {data
          // .filter((item, ind) => slideIndex == ind)
          .map(
            (item, ind) =>
              slideIndex === ind && (
                <div
                  className="mySlides"
                  style={{
                    display: slideIndex === ind ? "flex" : "none",
                    justifyContent: "center",
                    alignItems: "center",
                    overflow: "hidden",
                  }}
                >
                  <div className="numbertext">{`${ind + 1} / ${
                    data.length
                  }`}</div>
                  {item.type === "video" ? (
                    <iframe
                      width="100%"
                      height="100%"
                      src={item.url}
                      title="YouTube video player"
                      frameborder="0"
                      allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                      allowfullscreen
                      style={{
                        display: slideIndex === ind ? "block" : "none",
                      }}
                    ></iframe>
                  ) : item.type === "image" ? (
                    <img
                      src={item.url}
                      alt=""
                      style={{
                        flexShrink: 0,
                        minWidth: "100%",
                        minHeight: "100%",
                      }}
                    />
                  ) : null}
                </div>
              )
          )}

        <button
          className="prev"
          onClick={() => {
            plusSlides(-1);
          }}
        >
          ❮
        </button>
        <button
          className="next"
          onClick={() => {
            plusSlides(1);
          }}
        >
          ❯
        </button>
      </div>
      <div className="description">
        <h3 className="gradient-text blue ">{data[slideIndex].description}</h3>
      </div>
    </div>
  );
}

export default SlideShow;
