import React from "react";
import "./index.scss";
import SlideShow from "./SlideShow";
import { Modal, Button } from "react-bootstrap";
function SlideShowModal({ modalShow, modalOnhide, data }) {
  return (
    <Modal
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      show={modalShow}
      onHide={modalOnhide}
      className="useCase--Modal"
    >
      <Modal.Body>
        <SlideShow data={data} />{" "}
      </Modal.Body>
      <Modal.Footer>
        <Button onClick={modalOnhide}>Close</Button>
      </Modal.Footer>
    </Modal>
  );
}

export default SlideShowModal;
