import React, { useState } from "react";
import { useParams } from "react-router";
import SlideShowModal from "../Components/SlideShowModal/index";
import { projectsData } from "./projectData";

function ProjectsSection() {
  const [showModal, setShowModal] = useState(false);
  const { case: useCase } = useParams();
  const data = projectsData[useCase] || Object.values(projectsData).flat();
  const [slideShowData, setSlideShowData] = useState([]);
  const isDevOps = useCase === 'devops';
  
  const routeToTitle = () => {
    switch(useCase) {
      case 'ai':
        return 'Computer Vision & Machine Learning'
      case 'apps':
        return 'Applications';
      case 'robotics':
        return 'Robotics';
      case 'iot':
        return 'Internet Of Things';
      case 'devops':
        return 'DevOps';
      default:
        return '';
    }
  };

  const pageTitle = useCase ? routeToTitle() : 'Discover Our Projects';

  return (
    <div>
      <div className="container section-padding-0-70">
        <div className="section-heading text-center">
          <div
            className="dream-dots justify-content-center fadeInUp"
            data-wow-delay="0.2s"
          >
            <span className="gradient-text">{pageTitle}</span>
          </div>
          {/* <h2 className="wow fadeInUp" data-wow-delay="0.3s">
            Get Your Project
          </h2> */}
          <p className="wow fadeInUp" data-wow-delay="0.4s">
          </p>
        </div>
        <div className="row">
          {!isDevOps ? data.map((item) => (
              <div className="col-12 col-lg-6" key={item.title}>
                <div className="api-box has-shadow">
                  <h3 className="bold mb-15">{item.title}</h3>
                  <p>{item.description}</p>
                  {
                    !item.noDemo ?
                      <button
                        onClick={() => {
                          setShowModal(true);
                          setSlideShowData(item.data);
                        }}
                        className="btn more-btn pink mt-15"
                      >
                        Learn More
                      </button>
                      :
                      null
                  }
                </div>
              </div>
            )) 
            :
            <p className="fadeInUp" data-wow-delay="0.4s">
              We incorporate DevOps in almost all our projects from the simple task of setting up version management to more complicated tasks such as setting up continuous integration, continuous deployment and access management pipelines.
            </p>
          }
        </div>
      </div>
      <SlideShowModal
        modalShow={showModal}
        modalOnhide={() => {
          setShowModal(false);
        }}
        data={slideShowData}
      />
    </div>
  );
}

export default ProjectsSection;
